import { createCookieSessionStorage } from '@remix-run/node';
import invariant from 'tiny-invariant';
import { redirect } from '@remix-run/node';
import { getUserById } from '~/models/user.server';

export async function getUserId(request: Request): Promise<string | undefined> {
  const { session } = await getSession(request);
  const userId = session.get(USER_SESSION_KEY);
  return userId;
}

export async function requireUserId(
  request: Request,
  redirectTo: string = new URL(request.url).pathname,
): Promise<string> {
  const userId = await getUserId(request);
  if (!userId) {
    const searchParams = new URLSearchParams([['redirectTo', redirectTo]]);
    throw redirect(`/login?${searchParams}`);
  }
  return userId;
}

export async function requireUser(request: Request) {
  const userId = await requireUserId(request);

  const user = await getUserById(userId);
  if (user) return user;

  throw await logout(request);
}

export async function logout(request: Request) {
  const { session } = await getSession(request);
  return redirect('/', {
    headers: {
      'Set-Cookie': await cookieSessionStorage.destroySession(session),
    },
  });
}

export async function getUser(request: Request): Promise<null | User> {
  const userId = await getUserId(request);
  if (userId === undefined) return null;

  const user = await getUserById(userId);

  if (user) return user;

  throw await logout(request);
}

export async function getSession(request: Request) {
  const cookie = request.headers.get('Cookie');

  const session = await cookieSessionStorage.getSession(cookie);
  return {
    session,
  };
}

export async function createUserSession({
  request,
  userId,
  remember,
  redirectTo,
}: {
  request: Request;
  userId: string;
  remember: boolean;
  redirectTo: string;
}) {
  const { session } = await getSession(request);
  session.set(USER_SESSION_KEY, userId);

  return redirect(redirectTo, {
    headers: {
      'Set-Cookie': await cookieSessionStorage.commitSession(session, {
        maxAge: remember
          ? 60 * 60 * 24 * 7 // 7 days
          : undefined,
      }),
    },
  });
}

invariant(process.env.SESSION_SECRET, 'SESSION_SECRET must be set');

export const cookieSessionStorage = createCookieSessionStorage({
  cookie: {
    name: '__session',
    httpOnly: true,
    path: '/',
    sameSite: 'lax',
    secrets: [process.env.SESSION_SECRET],
    secure: process.env.NODE_ENV === 'production',
  },
});
export const USER_SESSION_KEY = 'userId';
